import { filter } from 'bluebird'

const imagedataFilters = require('imagedata-filters')
const Promise = require('bluebird')
const Storage = require('./../lib/storage')
const Rank = require('./rank')

const LockKey = '_group_'
const ExtDefaultIcon = './../assets/default-icon.png'
const ExtDefaultIconFilter = './../assets/default-icon-filter.png'
const ExtDefaultIconDinginess = './../assets/default-icon-dinginess.png'
const ExtDefaultColor = '#5c5e6f'

// 储存扩展列表
let allExtList = []
// Vue 实例
let vm = null

// let bg = chrome.extension.getBackgroundPage();

/**
 * 对扩展对象统一进行 “着颜色”
 * 防止频繁改动数据触发 Watcher
 */
let setExtColorDefer = (function () {
  let timer = null
  let temp = []
  return function (item, obj) {
    temp.push({
      item,
      obj,
    })
    if (timer) {
      clearTimeout(timer)
      timer = null
    }
    timer = setTimeout(() => {
      // console.log('== setExtColorDefer Timer ==')
      temp.forEach((element) => {
        element['item'] = Object.assign(element['item'], element['obj'])
      })
      clearTimeout(timer)
      timer = null
      temp = []
    }, 30)
  }
})()

/**
 * [getColor 获取扩展图标的平均色值，用于扩展名称显示的底色]
 */
function getExtColor(item) {
  // 同步方法 使用chrome.storage.local存储
  let getImageColor2 = function (img) {
    let _imageColor_key = '_imageColor_' + item.id
    let res = new Promise((resolve, reject) => {
      chrome.storage.local.get(_imageColor_key, function (itemColor) {
        if (itemColor[_imageColor_key] && itemColor[_imageColor_key].version == item.version) {
          resolve(itemColor[_imageColor_key])
        } else {
          let canvas = document.createElement('canvas')
          canvas.width = img.width
          canvas.height = img.height

          let context = canvas.getContext('2d')

          context.drawImage(img, 0, 0)

          let originalBase64 = canvas.toDataURL()

          // 获取像素数据
          let imageData = context.getImageData(0, 0, img.width, img.height)
          let data = imageData.data

          let r = 0
          let g = 0
          let b = 0

          // 浅色阀值
          let lightColor = 180

          let substantialColor = 1000

          // 取所有像素的平均值
          for (let row = 0; row < img.height; row++) {
            for (let col = 0; col < img.width; col++) {
              let r1 = data[(img.width * row + col) * 4]
              let g1 = data[(img.width * row + col) * 4 + 1]
              let b1 = data[(img.width * row + col) * 4 + 2]

              // 获取图片有效色值位置
              if (!(r1 == 255 && g1 == 255 && b1 == 255)) {
                if (col < substantialColor) {
                  substantialColor = col
                }
              }

              if (!(r1 > lightColor && g1 > lightColor && b1 > lightColor)) {
                r += r1
                g += g1
                b += b1
              }
            }
          }

          // 求取平均值
          r /= img.width * img.height
          g /= img.width * img.height
          b /= img.width * img.height

          // 将最终的值取整
          r = Math.round(r)
          g = Math.round(g)
          b = Math.round(b)

          let newColor = 'rgb(' + r + ',' + g + ',' + b + ')'
          if (r > lightColor && g > lightColor && b > lightColor) {
            newColor = ExtDefaultColor
          }

          // 普通滤镜 - 1
          let filterImageData = imagedataFilters.default.grayscale(imageData, { amount: '1' })
          filterImageData = imagedataFilters.default.opacity(filterImageData, { amount: '.5' })
          context.putImageData(filterImageData, 0, 0)
          let filterBase64 = canvas.toDataURL()
          // Dinginess滤镜 - 2
          filterImageData = imagedataFilters.default.opacity(filterImageData, { amount: '.2' })
          context.putImageData(filterImageData, 0, 0)
          let dinginessBase64 = canvas.toDataURL()

          // dark模式处理
          let originalBase64Dark = originalBase64
          let filterBase64Dark = filterBase64
          let dinginessBase64Dark = dinginessBase64

          let luma = 0.2126 * r + 0.7152 * g + 0.0722 * b
          // 深色阀值
          if (luma < 5) {
            // imageData脏了 重新画
            context.drawImage(img, 0, 0)
            imageData = context.getImageData(0, 0, img.width, img.height)
            // console.log(item.shortName + '-' + r + ':' + g + ':' + b + '-' + luma)
            // 重画图像
            let customImageData = grayImageScale(imageData, item)
            context.putImageData(customImageData, 0, 0)
            filterBase64Dark = canvas.toDataURL()

            // let filterImageDataDark = imagedataFilters.default.grayscale(imageData, {amount: '1'})
            // filterImageDataDark = imagedataFilters.default.brightness(filterImageDataDark, {amount: '2'})

            let originalImageDataDark = imagedataFilters.default.brightness(imageData, {
              amount: '2',
            })
            context.putImageData(originalImageDataDark, 0, 0)
            originalBase64Dark = canvas.toDataURL()

            // Dinginess滤镜 - 2
            originalImageDataDark = imagedataFilters.default.opacity(originalImageDataDark, {
              amount: '.2',
            })
            context.putImageData(originalImageDataDark, 0, 0)
            dinginessBase64Dark = canvas.toDataURL()
          }

          let itemColorItem = {
            version: item.version,
            color: newColor,
            substantial: substantialColor,
            originalBase64,
            filterBase64,
            dinginessBase64,
            originalBase64Dark,
            filterBase64Dark,
            dinginessBase64Dark,
          }
          chrome.storage.local.set({ [_imageColor_key]: itemColorItem }, function () {
            if (chrome.runtime.lastError) {
              // console.log("Error Storing: " + chrome.runtime.lastError);
            } else {
              // console.log("Success Storing");
            }
          })
          resolve(itemColorItem)
        }
      })
    })
    return res
  }

  let getImageColor = function (img) {
    let itemColor = localStorage.getItem('_imageColor_' + item.id)
    // 如果没有originalBase64Dark 说明没有设置过黑暗模式
    if (
      itemColor &&
      JSON.parse(itemColor).version === item.version &&
      JSON.parse(itemColor).originalBase64Dark
    ) {
      // empty
    } else {
      let canvas = document.createElement('canvas')
      canvas.width = img.width
      canvas.height = img.height

      let context = canvas.getContext('2d')

      context.drawImage(img, 0, 0)

      let originalBase64 = canvas.toDataURL()

      // 获取像素数据
      let imageData = context.getImageData(0, 0, img.width, img.height)
      let data = imageData.data

      let r = 0
      let g = 0
      let b = 0

      // 浅色阀值
      let lightColor = 180

      let substantialColor = 1000

      // 取所有像素的平均值
      for (let row = 0; row < img.height; row++) {
        for (let col = 0; col < img.width; col++) {
          let r1 = data[(img.width * row + col) * 4]
          let g1 = data[(img.width * row + col) * 4 + 1]
          let b1 = data[(img.width * row + col) * 4 + 2]

          // 获取图片有效色值位置
          if (!(r1 == 255 && g1 == 255 && b1 == 255)) {
            if (col < substantialColor) {
              substantialColor = col
            }
          }

          if (!(r1 > lightColor && g1 > lightColor && b1 > lightColor)) {
            r += r1
            g += g1
            b += b1
          }
        }
      }

      // 求取平均值
      r /= img.width * img.height
      g /= img.width * img.height
      b /= img.width * img.height

      // 将最终的值取整
      r = Math.round(r)
      g = Math.round(g)
      b = Math.round(b)

      let newColor = 'rgb(' + r + ',' + g + ',' + b + ')'
      if (r > lightColor && g > lightColor && b > lightColor) {
        newColor = ExtDefaultColor
      }

      // 普通滤镜 - 1
      let filterImageData = imagedataFilters.default.grayscale(imageData, { amount: '1' })
      filterImageData = imagedataFilters.default.opacity(filterImageData, { amount: '.5' })
      context.putImageData(filterImageData, 0, 0)
      let filterBase64 = canvas.toDataURL()
      // Dinginess滤镜 - 2
      filterImageData = imagedataFilters.default.opacity(filterImageData, { amount: '.2' })
      context.putImageData(filterImageData, 0, 0)
      let dinginessBase64 = canvas.toDataURL()

      // dark模式处理
      let originalBase64Dark = originalBase64
      let filterBase64Dark = filterBase64
      let dinginessBase64Dark = dinginessBase64

      let luma = 0.2126 * r + 0.7152 * g + 0.0722 * b
      // 深色阀值
      if (luma < 5) {
        // imageData脏了 重新画
        context.drawImage(img, 0, 0)
        imageData = context.getImageData(0, 0, img.width, img.height)
        // console.log(item.shortName + '-' + r + ':' + g + ':' + b + '-' + luma)
        // 重画图像
        let customImageData = grayImageScale(imageData, item)
        context.putImageData(customImageData, 0, 0)
        filterBase64Dark = canvas.toDataURL()

        // let filterImageDataDark = imagedataFilters.default.grayscale(imageData, {amount: '1'})
        // filterImageDataDark = imagedataFilters.default.brightness(filterImageDataDark, {amount: '2'})

        let originalImageDataDark = imagedataFilters.default.brightness(imageData, { amount: '2' })
        context.putImageData(originalImageDataDark, 0, 0)
        originalBase64Dark = canvas.toDataURL()

        // Dinginess滤镜 - 2
        originalImageDataDark = imagedataFilters.default.opacity(originalImageDataDark, {
          amount: '.2',
        })
        context.putImageData(originalImageDataDark, 0, 0)
        dinginessBase64Dark = canvas.toDataURL()
      }

      localStorage.setItem(
        '_imageColor_' + item.id,
        JSON.stringify({
          version: item.version,
          color: newColor,
          substantial: substantialColor,
          originalBase64,
          filterBase64,
          dinginessBase64,
          originalBase64Dark,
          filterBase64Dark,
          dinginessBase64Dark,
        })
      )
    }
    return JSON.parse(localStorage.getItem('_imageColor_' + item.id))
  }

  let img = new Image()
  img.crossOrigin = 'Anonymous'
  img.src = item.showIcon

  img.onload = async function () {
    let imgData = await getImageColor2(img)
    let extraObj = {}
    if (imgData.substantial === 1000) {
      extraObj['showIcon'] = ExtDefaultIcon
      extraObj['showBase64'] = {
        original: ExtDefaultIcon,
        filter: ExtDefaultIconFilter,
        dinginess: ExtDefaultIconDinginess,
      }
      extraObj['showColor'] = {
        original: ExtDefaultColor,
        filter: ExtDefaultColor,
        dinginess: ExtDefaultColor,
      }
      extraObj['showMark'] = 'original'
    } else {
      extraObj['showBase64'] = {
        original: imgData.originalBase64,
        filter: imgData.filterBase64,
        dinginess: imgData.dinginessBase64,
        originaldark: imgData.originalBase64Dark,
        filterdark: imgData.filterBase64Dark,
        dinginessdark: imgData.dinginessBase64Dark,
      }
      extraObj['showColor'] = {
        original: imgData.color,
        filter: '#b1b1b1',
        dinginess: '#f3f3f3',
      }
      extraObj['showMark'] = 'original'
    }
    setExtColorDefer(item, extraObj)
  }
}

function grayImageScale(imagedata, item) {
  // console.log(item.shortName + " change")
  var i = 0
  var data = imagedata.data
  var len = data.length
  var r, g, b
  for (; i < len; i += 4) {
    r = data[i]
    g = data[i + 1]
    b = data[i + 2]

    if (r >= 0 && r < 60 && g >= 0 && g < 60 && b >= 0 && b < 60) {
      data[i] = 75
      data[i + 1] = 75
      data[i + 2] = 75
    }
  }
  return imagedata
}

/**
 * 扩展排序方法
 */
function orderHandle(storage) {
  if (Storage.get('_radio_ext_sort_') === 'rank') {
    // 根据点击rank进行排序
    return function (b, a) {
      return Rank.get(a.id, storage) - Rank.get(b.id, storage)
    }
  } else {
    // 根据名称排序
    // 如果设置别名功能打开
    if (Storage.get('_switch_show_name_') === 'open') {
      return function (a, b) {
        var _a = a.nickName.charAt(0)
        var _b = b.nickName.charAt(0)
        return _a.localeCompare(_b, 'zh-Hans-CN', {
          sensitivity: 'accent',
          usage: 'search',
        })
      }
    } else {
      return function (a, b) {
        var _a = a.shortName.charAt(0)
        var _b = b.shortName.charAt(0)
        return _a.localeCompare(_b, 'zh-Hans-CN', {
          sensitivity: 'accent',
          usage: 'search',
        })
      }
    }
  }
}

/**
 * 排除扩展，管理器本身以及主题、皮肤等
 * @param {*} all
 */
function processHandle(all, filter) {
  let res = new Promise((resolve, reject) => {
    // 当前扩展管理器信息
    let appId = chrome.runtime.id || { id: '' }

    all.forEach((item) => {
      if (item.id !== appId && item.type !== 'theme') {
        // 处理显示图标
        if (item.icons && item.icons.length > 0) {
          // if(item.id === 'ogdlpmhglpejoiomcodnpjnfgcpmgale') {
          //   console.error(item.icons[item.icons.length - 1].url)
          // }
          item.showIcon = item.icons[item.icons.length - 1].url
        } else {
          item.showIcon = ExtDefaultIcon
        }

        // 判断是否为锁定图标
        item.isLocked = false

        // 判断是否为应用或开发版本
        if (item.isApp) {
          item.showType = 'APP'
        } else if (item.installType === 'development') {
          item.showType = 'DEV'
        }

        // 是否处于hover状态
        item.isHover = false

        // 是否被搜索关键词命中
        item.isSearched = false

        item['showBase64'] = {
          original: null,
          filter: null,
          dinginess: null,
        }
        item['showColor'] = {
          original: '#f9f9f9',
          filter: '#f9f9f9',
          dinginess: '#f9f9f9',
        }
        item['showMark'] = 'original'

        item.nickName = getNickName(item.id, item.shortName)

        if (filter && item.isApp && Storage.get('_switch_show_app_') == 'close') {
          // empty
        } else {
          allExtList.push(item)
          setTimeout(() => {
            // 处理扩展的图标、平均颜色等
            getExtColor(item)
          }, 0)
        }
      }
    })

    resolve(allExtList)
  })
  return res
}

function getNickName(itemid, itemname) {
  let nickName = localStorage.getItem('_itemNick_' + itemid)
  if (nickName) {
    // empty
  } else {
    nickName = formatNickName(itemname, 8)
    localStorage.setItem('_itemNick_' + itemid, nickName)
  }
  return nickName
}

function formatNickName(text, length) {
  text = text.replace(/\s+/g, '')
  if (text.replace(/[\u4e00-\u9fa5]/g, 'aa').length <= length) {
    return text
  } else {
    var _length = 0
    var outputText = ''
    for (var i = 0; i < text.length; i++) {
      if (/[\u4e00-\u9fa5]/.test(text[i])) {
        _length += 2
      } else {
        _length += 1
      }
      if (_length > length) {
        break
      } else {
        outputText += text[i]
      }
    }
    return outputText
  }
}

/**
 * [addIconBadge 给扩展图标添加角标，针对未加锁需要平时关闭的]
 */
function addIconBadge() {
  if (Storage.get('_switch_show_badge_') !== 'close') {
    let badgeList = allExtList.filter((item) => {
      if (item.isLocked !== item.enabled && !item.isFixed && !item.isRuled) {
        return true
      }
    })

    if (badgeList.length === 0) {
      // 关闭清理动画
      if (vm && vm.ext) {
        vm.ext.iconBadgeAnim = false
      }
      chrome.action.setBadgeText({ text: '' })
      return false
    } else {
      // 显示清理动画
      if (vm && vm.ext) {
        vm.ext.iconBadgeAnim = true
      }
      chrome.action.setBadgeBackgroundColor({ color: '#f44336' })
      chrome.action.setBadgeText({ text: badgeList.length.toString() })
      return true
    }
  } else {
    chrome.action.setBadgeText({ text: '' })
    return false
  }
}

/**
 * 获取所有安装的扩展
 * 异步操作，支持Promise
 */
function getAll(ins, filter) {
  vm = ins
  let res = new Promise((resolve, reject) => {
    allExtList = []
    chrome.management.getAll(function (obj) {
      resolve(processHandle(obj, filter))
    })
  })
  return res
}

/**
 * 启用或禁用扩展
 */
function onoff(item, callback) {
  // 更新对象状态属性
  item.enabled = !item.enabled
  // 重置Hover
  item.isHover = false
  // 同步至浏览器
  chrome.management.setEnabled(item.id, item.enabled, () => {
    // bg.updateState(item.id, item.enabled)
    callback && callback()
  })
}

/**
 * 启用扩展
 */
function on(item, callback) {
  // 更新对象状态属性
  item.enabled = true
  // 同步至浏览器
  chrome.management.setEnabled(item.id, true, () => {
    // bg.updateState(item.id, true)
    callback && callback()
  })
}

/**
 * 关闭扩展
 */
function off(item, callback) {
  // 更新对象状态属性
  item.enabled = false
  // 同步至浏览器
  chrome.management.setEnabled(item.id, false, () => {
    // bg.updateState(item.id, false)
    callback && callback()
  })
}

/**
 * 加锁、解锁扩展操作
 */
function lock(item) {
  item.isLocked = true

  // 锁定的时候要不要启用扩展呢
  // onoff(item)

  let group = Storage.get(LockKey)
  group.list[vm.groupIndex].lock[item.id] = 1
  Storage.set(LockKey, group)
}
// 解锁
function unlock(item) {
  item.isLocked = false
  let group = Storage.get(LockKey)
  delete group.list[vm.groupIndex].lock[item.id]
  Storage.set(LockKey, group)
}

/**
 * 卸载应用或扩展
 */
function uninstall(item) {
  chrome.management.uninstall(item.id, () => {
    chrome.management.get(item.id, (res) => {
      if (!res) {
        let index = allExtList.indexOf(item)
        allExtList.splice(index, 1)
      }
    })
  })
}

/**
 * 根据锁定状态，进行清理
 */
function clear() {
  allExtList.forEach((item) => {
    // 固定启动组处理
    if (item.isFixed && !item.enabled) {
      on(item)
    }

    if (item.isRuled && !item.enabled) {
      on(item)
    }

    if (item.isLocked === !item.enabled && !item.isFixed && !item.isRuled) {
      onoff(item)
    }
  })
}

export { addIconBadge, getAll, onoff, lock, unlock, uninstall, clear, orderHandle, on, off }
