<template>
  <div id="option">
    <div id="sidebar">
      <div id="logo">
        <span></span>
      </div>
      <TreeMenu :name="menuName"></TreeMenu>
    </div>
    <Dialog
      v-model="showDialog"
      :dialogWidth="dialogWidth"
      :dialogTitle="dialogTitle"
      :dialogContent="dialogContent"
      :confirmText="confirmText"
      :dangerText="dangerText"
      :cancelText="cancelText"
      :type="dialogType"
      v-on:cancel="clickCancel()"
      @danger="clickDanger()"
    ></Dialog>
    <div id="content">
      <!-- Tips -->
      <div id="_TOOLS_STATUS__" :class="tips.show ? 'tips show' : 'tips'">{{ tips.content }}</div>
      <div id="general" class="boxWrapper" v-show="generalShow">
        <!-- 设置显示模式 -->
        <div class="box">
          <h1>
            <span>{{ i18n.showMod }}</span>
          </h1>
          <ul class="radio-btn">
            <li>
              <label>
                <input
                  type="radio"
                  name="ext_view"
                  value="row"
                  v-model="viewMod"
                  @click="changeViewMod('row')"
                />
                <span class="radio-btn-input"></span>
                <span>{{ i18n.showGridMod }}</span>
              </label>
            </li>
            <li>
              <label>
                <input
                  type="radio"
                  name="ext_view"
                  value="column"
                  v-model="viewMod"
                  @click="changeViewMod('column')"
                />
                <span class="radio-btn-input"></span>
                <span>{{ i18n.showListMod }}</span>
              </label>
            </li>
          </ul>
        </div>

        <!-- 外观设置 -->
        <div class="box">
          <h1>
            <span>{{ i18n.appearance }}</span>
          </h1>
          <ul class="radio-btn">
            <li>
              <label>
                <input
                  type="radio"
                  name="ext_appearance"
                  value="light"
                  v-model="themeType"
                  @click="changeAppearance('light')"
                />
                <span class="radio-btn-input"></span>
                <span>{{ i18n.light }}</span>
              </label>
            </li>
            <li>
              <label>
                <input
                  type="radio"
                  name="ext_appearance"
                  value="dark"
                  v-model="themeType"
                  @click="changeAppearance('dark')"
                />
                <span class="radio-btn-input"></span>
                <span>{{ i18n.dark }}</span>
              </label>
            </li>
            <li>
              <label>
                <input
                  type="radio"
                  name="ext_appearance"
                  value="automatic"
                  v-model="themeType"
                  @click="changeAppearance('automatic')"
                />
                <span class="radio-btn-input"></span>
                <span>{{ i18n.automatic }}</span>
              </label>
            </li>
          </ul>
        </div>

        <!-- 设置显示列数 -->
        <div class="box">
          <h1>
            <span>{{ i18n.showCols }}</span>
            <span>{{ showWindowSize }}</span>
          </h1>
          <p>
            <input
              id="windowSizeRange"
              type="range"
              class="range-style"
              min="6"
              max="9"
              step="1"
              :value="showWindowSize"
              @input="changeWindowSize"
            />
          </p>
        </div>

        <!-- 设置图标大小 -->
        <div class="box">
          <h1>
            <span>{{ i18n.iconSize }}</span>
            <span>{{ getShowIconSize }}</span>
          </h1>
          <p>
            <input
              id="iconSizeRange"
              type="range"
              class="range-style"
              min="1"
              max="3"
              step="1"
              :value="showIconSize"
              @input="changeIconSize"
            />
          </p>
        </div>

        <!-- 是否显示角标 -->
        <div class="box">
          <h1>
            <span>{{ i18n.showBadgeName }}</span>
            <span
              class="tipdesc"
              @mouseenter="showTipDesc('badgeTip')"
              @mouseleave="hideTipDesc('badgeTip')"
            ></span>
            <div
              id="badgeTip"
              class="tipArea"
              @mouseenter="tipDescEnter('badgeTip')"
              @mouseleave="tipDescLeave('badgeTip')"
            >
              {{ i18n.showBadgeDesc }}
            </div>
          </h1>
          <switch-btn data-key="show_badge"></switch-btn>
          <!-- <p class="describe">{{i18n.showBadgeDesc}}</p> -->
        </div>

        <!-- 是否显示快捷图标 -->
        <div class="box">
          <h1>
            <span>{{ i18n.showFastName }}</span>
            <span
              class="tipdesc"
              @mouseenter="showTipDesc('fastTip')"
              @mouseleave="hideTipDesc('fastTip')"
            ></span>
            <div
              id="fastTip"
              class="tipArea"
              @mouseenter="tipDescEnter('fastTip')"
              @mouseleave="tipDescLeave('fastTip')"
            >
              {{ i18n.showFastDesc }}
            </div>
          </h1>
          <switch-btn data-key="show_fast"></switch-btn>
          <!-- <p class="describe">{{i18n.showFastDesc}}</p> -->
        </div>

        <!-- 是否显示扩展名称 -->
        <div class="box">
          <h1>
            <span>{{ i18n.showGridExtName }}</span>
            <span
              class="tipdesc"
              @mouseenter="showTipDesc('extNameTip')"
              @mouseleave="hideTipDesc('extNameTip')"
            ></span>
            <div
              id="extNameTip"
              class="tipArea"
              @mouseenter="tipDescEnter('extNameTip')"
              @mouseleave="tipDescLeave('extNameTip')"
            >
              1.{{ i18n.showGridExtDesc }}<br />2.{{ i18n.showGridExtTip }}
            </div>
          </h1>
          <switch-btn data-key="show_name"></switch-btn>
        </div>

        <!-- 设置扩展图标排序 -->
        <div class="box">
          <h1>{{ i18n.rankName }}</h1>
          <ul class="radio-btn">
            <li>
              <label>
                <input
                  type="radio"
                  name="ext_sort"
                  value="name"
                  v-model="sortType"
                  @click="changeSortType('name')"
                />
                <span class="radio-btn-input"></span>
                <span>{{ i18n.sortByName }}</span>
              </label>
            </li>
            <li>
              <label>
                <input
                  type="radio"
                  name="ext_sort"
                  value="rank"
                  v-model="sortType"
                  @click="changeSortType('rank')"
                />
                <span class="radio-btn-input"></span>
                <span>{{ i18n.sortByRank }}</span>
              </label>
              <button @click="resetRank">{{ i18n.rankBtn }}</button>
            </li>
          </ul>
        </div>

        <!-- 备份 -->
        <div class="box">
          <h1>
            <span>{{ i18n.backup }}</span>
            <span
              class="tipdesc"
              @mouseenter="showTipDesc('backupTip')"
              @mouseleave="hideTipDesc('backupTip')"
            ></span>
            <div
              id="backupTip"
              class="tipArea"
              @mouseenter="tipDescEnter('backupTip')"
              @mouseleave="tipDescLeave('backupTip')"
            >
              {{ i18n.backupDesc }}
            </div>
          </h1>
          <input
            id="uploadOption"
            type="file"
            @change="importOption"
            style="display: none"
            accept=".options"
          />
          <button style="color: #fff" @click="exportOption()">{{ i18n.export_options }}</button
          ><button style="color: #fff; margin-left: 10px">
            <label for="uploadOption">{{ i18n.import_options }}</label>
          </button>
          <!-- <p class="describe">{{i18n.backupDesc}}</p> -->
        </div>

        <!-- 设置扩展菜单弹出方式 -->
        <div class="box">
          <h1>{{ i18n.menuType }}</h1>
          <ul class="radio-btn">
            <li>
              <label>
                <input
                  type="radio"
                  name="ext_menu"
                  value="hover"
                  v-model="menuType"
                  @click="changeMenuType('hover')"
                />
                <span class="radio-btn-input"></span>
                <span>{{ i18n.hoverMenu }}</span>
              </label>
            </li>
            <li>
              <label>
                <input
                  type="radio"
                  name="ext_menu"
                  value="click"
                  v-model="menuType"
                  @click="changeMenuType('click')"
                />
                <span class="radio-btn-input"></span>
                <span>{{ i18n.clickMenu }}</span>
              </label>
            </li>
          </ul>
        </div>

        <!-- 是否显示应用 -->
        <div class="box">
          <h1>{{ i18n.showApp }}</h1>
          <switch-btn data-key="show_app"></switch-btn>
        </div>
      </div>

      <!-- 配置分组 -->
      <div id="group" class="boxGroup" v-show="group1 || group2">
        <div id="group1" v-if="group1">
          <h2>{{ i18n.optionGroupDesc }}</h2>
          <p class="describe" style="margin: 30px 0 0 0">{{ i18n.optionGroupOperat }}</p>
          <ul class="group-list gclearfix">
            <li
              v-for="(item, index) in group.list"
              :key="index"
              :class="index === groupIndex ? 'cur' : ''"
              @click="selectGroup(index)"
            >
              {{ item.name }}
              <i class="group-del" @click.stop="deleteGroup(index)"></i>
              <i class="group-mod" @click.stop="modifyGroup(index)"></i>
            </li>
            <li class="group-add" @click="addGroup">
              <svg
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                xmlns:xlink="http://www.w3.org/1999/xlink"
              >
                <path
                  d="M939.939489 459.072557 562.339502 459.072557 562.339502 83.519182 462.055494 83.519182 462.055494 459.072557 84.455507 459.072557 84.455507 559.356564 462.055494 559.356564 462.055494 939.003164 562.339502 939.003164 562.339502 559.356564 939.939489 559.356564Z"
                  fill="#636363"
                ></path>
              </svg>
            </li>
          </ul>
          <ext-item4 :data-list="getAllExtList" from="option">
            <template slot="empty">
              <li class="empty" v-if="getAllExtList.length === 0">{{ i18n.emptyShowListCon }}</li>
            </template>
          </ext-item4>
          <div
            id="rightMenu"
            :class="[rightMenu.showClass]"
            :style="{ left: rightMenu.left, right: rightMenu.right, top: rightMenu.top }"
            @mouseleave="rightMemuLeave"
          >
            <div class="name" :style="{ background: rightMenu.backgroundColor }">
              {{ rightMenu.name }}
            </div>
          </div>
        </div>

        <div id="group2" v-if="group2">
          <h2>{{ i18n.fixGroupDesc }}</h2>
          <p class="describe" style="margin: 30px 0 20px 0">{{ i18n.fixGroupOperat }}</p>
          <ExtItem3 :data-list="getAllExtList" from="fix">
            <template slot="empty">
              <li class="empty" v-if="getAllExtList.length === 0">{{ i18n.emptyShowListCon }}</li>
            </template>
          </ExtItem3>

          <div
            id="rightMenu"
            :class="[rightMenu.showClass]"
            :style="{ left: rightMenu.left, right: rightMenu.right, top: rightMenu.top }"
            @mouseleave="rightMemuLeave"
          >
            <div class="name" :style="{ background: rightMenu.backgroundColor }">
              {{ rightMenu.name }}
            </div>
          </div>
        </div>
      </div>

      <!-- 配置别名 -->
      <div id="group3" class="boxGroup" v-if="nameShow">
        <h2>{{ i18n.nameExtDesc }}</h2>
        <p class="describe" style="margin: 30px 0 0 0">{{ i18n.nameExtOperat }}</p>
        <ExtItem5 :data-list="getAllExtList" from="name">
          <template slot="empty">
            <li class="empty" v-if="getAllExtList.length === 0">{{ i18n.emptyShowListCon }}</li>
          </template>
        </ExtItem5>
      </div>

      <!-- 配置规则 -->
      <div id="rule" class="boxGroup" v-if="ruleShow">
        <h2>
          {{ i18n.ruleExtDesc
          }}<span style="padding-left: 20px; font-size: 15px" @click="ruleTip()">{{
            i18n.ruleExtWhat
          }}</span>
        </h2>
        <p class="describe" style="margin: 30px 0 0 0">{{ i18n.ruleExtList }}</p>
        <table id="rounded-corner">
          <thead>
            <tr>
              <th scope="col" class="rounded-company">{{ i18n.ruleExtThNumber }}</th>
              <th scope="col" class="rounded-q1">{{ i18n.ruleExtThExt }}</th>
              <th scope="col" class="rounded-q2">{{ i18n.ruleExtThUrl }}</th>
              <th scope="col" class="rounded-q3">{{ i18n.ruleExtAction }}</th>
              <th scope="col" class="rounded-q4">{{ i18n.ruleExtThOperate }}</th>
            </tr>
          </thead>
          <tfoot>
            <tr>
              <td colspan="4" class="rounded-foot-left">
                <em
                  ><span class="lable_question" @click="matchTip()">{{ i18n.howToSet }}</span></em
                >
              </td>
              <td class="rounded-foot-right">&nbsp;</td>
            </tr>
          </tfoot>
          <tbody>
            <tr v-for="(item, index) in rules.list" :key="index">
              <td>{{ index + 1 }}</td>
              <td>
                <div v-for="(idv, idk) in item.ids" :key="idk">
                  <img :src="getItemIcon(idk)" alt="" />
                </div>
              </td>
              <td>{{ item.match.url }}</td>
              <td>{{ i18n[item.action] }}</td>
              <td>
                <svg
                  @click="editRule(item, index)"
                  t="1584615110000"
                  class="icon"
                  viewBox="0 0 1024 1024"
                  version="1.1"
                  xmlns="http://www.w3.org/2000/svg"
                  p-id="1483"
                  width="24"
                  height="24"
                >
                  <path
                    d="M800 531.52a32 32 0 0 1 64 0v305.92A90.56 90.56 0 0 1 773.44 928H186.88A90.88 90.88 0 0 1 96 837.44V250.88A90.88 90.88 0 0 1 186.88 160h288a32 32 0 0 1 0 64h-288A26.56 26.56 0 0 0 160 250.88v586.56A26.56 26.56 0 0 0 186.88 864h586.56A26.56 26.56 0 0 0 800 837.44z"
                    p-id="1484"
                    fill="#6494f4"
                  ></path>
                  <path
                    d="M825.28 209.92l-43.2-43.2-405.12 405.12-13.76 56.96 56.96-13.76zM444.16 675.2L327.36 704A32 32 0 0 1 288 664.64l28.16-116.8a32 32 0 0 1 8.32-15.04l424-423.04a48 48 0 0 1 67.2 0l66.56 66.88a47.68 47.68 0 0 1 0 66.88L459.2 666.56a33.92 33.92 0 0 1-15.04 8.64z"
