                </svg>
                <svg
                  @click="copyRule(item, index)"
                  t="1612252405152"
                  class="icon"
                  viewBox="0 0 1024 1024"
                  version="1.1"
                  xmlns="http://www.w3.org/2000/svg"
                  p-id="4717"
                  width="24"
                  height="24"
                >
                  <path
                    d="M855.990857 73.142857A95.817143 95.817143 0 0 1 950.857143 168.374857V707.730286A95.817143 95.817143 0 0 1 857.526857 804.571429H804.571429v40.374857c0 55.076571-39.204571 100.278857-89.380572 105.398857l-9.508571 0.512H172.032C117.394286 950.857143 73.142857 903.460571 73.142857 844.946286V325.339429C73.142857 266.825143 117.394286 219.428571 172.032 219.428571H219.428571v-49.444571A95.817143 95.817143 0 0 1 312.758857 73.142857h543.232zM705.682286 290.011429H172.032c-18.212571 0-32.914286 15.798857-32.914286 35.328v519.606857c0 19.529143 14.701714 35.328 32.914286 35.328h533.650286c18.212571 0 32.914286-15.798857 32.914285-35.328V325.339429c0-19.529143-14.701714-35.328-32.914285-35.328zM256 732.964571a36.571429 36.571429 0 1 1 0 73.142858 36.571429 36.571429 0 0 1 0-73.142858z m120.685714 0a36.571429 36.571429 0 1 1 0 73.142858 36.571429 36.571429 0 0 1 0-73.142858z m119.661715 0a36.571429 36.571429 0 1 1 0 73.142858 36.571429 36.571429 0 0 1 0-73.142858z m120.685714 0a36.571429 36.571429 0 1 1 0 73.142858 36.571429 36.571429 0 0 1 0-73.142858z m239.469714-589.238857H314.294857c-16.310857 0-28.672 12.141714-28.964571 25.892572V219.428571h420.352c51.419429 0 93.622857 41.984 98.450285 95.744l0.438858 10.166858v408.576h51.419428c16.310857 0 28.672-12.068571 28.964572-25.819429V169.984c0-13.750857-12.214857-25.965714-28.525715-26.258286zM256 621.933714a36.571429 36.571429 0 1 1 0 73.142857 36.571429 36.571429 0 0 1 0-73.142857zM256 512a36.571429 36.571429 0 1 1 0 73.142857 36.571429 36.571429 0 0 1 0-73.142857z"
                    fill="#6494f4"
                    p-id="4718"
                  ></path>
                </svg>
                <svg
                  @click="deleteRule(index)"
                  t="1584614647586"
