                <span style="padding-left: 10px">
                  <toggle-switch :value="!item.disabled" :param="index"></toggle-switch>
                </span>
              </td>
            </tr>
          </tbody>
        </table>
        <button style="margin-top: 10px; color: #fff; margin-right: 10px;" @click="addRule" v-show="!ruleExtShow">
          {{ i18n.ruleAdd }}
        </button>
        <button style="margin-top: 10px; color: #fff; margin-right: 10px;" @click="saveRule" v-show="ruleExtShow">
          {{ i18n.ruleSave }}
        </button>
        <button style="margin-top: 10px; color: #fff; margin-right: 10px;" @click="cancelRule" v-show="ruleExtShow">
          {{ i18n.cancel }}
        </button>
        <div class="newrule" v-show="ruleExtShow">
          <div class="line">
            <div class="chunk">{{ i18n.ruleExtAction }}</div>
            <div class="chunk">
              <select ref="ruleModel" id="ruleModel">
                <option value="enableOnly">{{ i18n.enableOnly }}</option>
                <option value="disableOnly">{{ i18n.disableOnly }}</option>
                <option value="enableWhen">{{ i18n.enableWhen }}</option>
                <option value="disableWhen">{{ i18n.disableWhen }}</option>
              </select>
            </div>
          </div>

          <div class="line">
            <div class="chunk">{{ i18n.ruleWildcard }}</div>
            <div class="chunk">
              <input id="fixUrl" value="http(s)://" readonly /><input id="matchUrl" ref="ruleUrl" />
            </div>
            <span class="lable_question" style="color: #6494f4">({{ i18n.ruleWildMulti }})</span>
          </div>

          <ExtItem6 :data-list="getAllRuleExtList" from="rule">
            <template slot="empty">
              <li class="empty" v-if="getAllRuleExtList.length === 0">
                {{ i18n.emptyShowListCon }}
              </li>
            </template>
          </ExtItem6>
        </div>
