        ids: {},
        match: {
          mod: '',
          url: '',
          regExp: '', //预编译
        },
        disabled: false,
      },
      ruleIndex: -1,

      groupIndex: 0,
      showIconSize: Common.ShowIconSize,
      showWindowSize: Common.WindowSizeDefaultColum,
      tips: {
        show: false,
        content: '',
      },
      sortType: 'name',
      theme: '',
      viewMod: 'row',
      themeType: 'light',
      menuType: 'hover',
      generalShow: false,
      supportShow: false,
      feedbackShow: false,
      nameShow: false,
      menuName: '',
      uploadedFiles: [],
      group1: false,
      group2: false,
      ruleShow: false,
      ruleExtShow: false,
      showDialog: false,
      dialogTitle: '',
      dialogContent: '',
      confirmText: '',
      dangerText: '',
      cancelText: '',
      dialogType: '',
      dialogWidth: '',
      tipHoverTimer: 1,
      tipHoverDoing: false,
    }
  },
  components: {
    SwitchBtn,
    TreeMenu,
    ExtItem3,
    ExtItem4,
    ExtItem5,
    ExtItem6,
    ToggleSwitch,
    Dialog,
  },
  watch: {
    'group.list': function (val, oldVal) {
      Storage.set('_group_', this.group)
    },
    // extList: {
    //   handler: (_new, _old) => {
    //     setTimeout(() => {
    //       Extension.addIconBadge()
    //     }, 0)
    //   },
    //   deep: true
    // }
  },
  computed: {
    // edge
    otherDesc() {
      return this.i18n.otherDesc.replace(
        '<a>',
        `<a href="https://chrome.google.com/webstore/detail/extension-manager/gjldcdngmdknpinoemndlidpcabkggco/reviews?hl=${this.language}" target="_blank">`
      )
      // return this.i18n.otherEdgeDesc.replace('<a>', `<a href="https://microsoftedge.microsoft.com/addons/detail/bhahgfgngfghgjhnpplmemebhenieijb?hl=${this.language}" target="_blank">`)
    },
    getAllExtList() {
      // return this.extList.map(item => {
      //   if (this.group.list[this.groupIndex].lock[item.id]) {
      //     item.isLocked = true
      //   } else {
      //     item.isLocked = false
      //   }
      //   return item
      // })
      let list = this.extList.map((item) => {
        if (this.group.list[this.groupIndex].lock[item.id]) {
          item.isLocked = true
        } else {
          item.isLocked = false
        }
        if (this.fix[item.id]) {
          item.isFixed = true
        } else {
          item.isFixed = false
        }
        return item
      })

      // let orderHandle = function(a, b) {
      //   var _a = a.shortName.charAt(0);
      //   var _b = b.shortName.charAt(0);

      //   if (a.isLocked && b.isLocked) {
      //     return _a.localeCompare(_b, "zh-Hans-CN", {
      //       sensitivity: "accent",
      //       usage: "search"
      //     });
      //   } else if (!a.isLocked && !b.isLocked) {
      //     return _a.localeCompare(_b, "zh-Hans-CN", {
      //       sensitivity: "accent",
      //       usage: "search"
      //     });
      //   } else {
      //     return a.isLocked > b.isLocked ? -1 : 1;
      //   }
      //   return _a.localeCompare(_b, "zh-Hans-CN", {
      //     sensitivity: "accent",
      //     usage: "search"
      //   });
      // };

      let orderHandle2 = function (a, b) {
        var _a = a.shortName.charAt(0)
        var _b = b.shortName.charAt(0)
        return _a.localeCompare(_b, 'zh-Hans-CN', {
          sensitivity: 'accent',
          usage: 'search',
        })
      }

      return list.sort(orderHandle2)
    },

    getAllRuleExtList() {
      let list
      if (this.ruleIndex === -1) {
        list = this.extList.map((item) => {
          item.isRuled = false
          return item
        })
      } else {
        list = this.extList.map((item) => {
          if (this.rules.list[this.ruleIndex].ids[item.id]) {
            item.isRuled = true
          } else {
            item.isRuled = false
          }
          return item
        })
      }

      let orderHandle2 = function (a, b) {
        var _a = a.shortName.charAt(0)
        var _b = b.shortName.charAt(0)
        return _a.localeCompare(_b, 'zh-Hans-CN', {
          sensitivity: 'accent',
          usage: 'search',
        })
      }

      return list.sort(orderHandle2)
    },

    getShowWindowSize() {
      // 默认情况下是7列
      return Common.WindowSizeByColum[this.showWindowSize || Common.WindowSizeDefaultColum]
    },
    getShowIconSize() {
      const IconSizeShowText = {
        1: this.i18n.sizeSmall,
        2: this.i18n.sizeNormal,
        3: this.i18n.sizeBig,
      }
      return IconSizeShowText[this.showIconSize]
    },
  },

  methods: {
    extEnter(item) {
      Util.enter(item)
    },

    extLeave(item) {
      Util.leave(item)
    },

    showTipDesc(tipid) {
      var keys = document.querySelectorAll('.tipArea')
      for (var i = 0; i < keys.length; ++i) {
        keys[i].style.visibility = 'hidden'
      }

      clearTimeout(this.tipHoverTimer)
      this.tipHoverTimer = null
      document.getElementById(tipid).style.visibility = 'visible'
    },

    hideTipDesc(tipid) {
      this.tipHoverTimer = setTimeout(() => {
        if (!this.tipHoverDoing) {
          document.getElementById(tipid).style.visibility = 'hidden'
        }
      }, 200)
    },

    tipDescEnter(tipid) {
      clearTimeout(this.tipHoverTimer)
      this.tipHoverTimer = null
      this.tipHoverDoing = true
    },

    tipDescLeave(tipid) {
      this.tipHoverDoing = false
      document.getElementById(tipid).style.visibility = 'hidden'
    },

    rightMemuLeave() {
      Util.resetHandle()
    },

    modifyGroup(index) {
      this.groupIndex = index

      let that = this
      setTimeout(() => {
        var newName = prompt(this.i18n.optionGroupModifyName) || 'New Group'
        if (newName.trim()) {
          let obj = this.group.list[index]
          obj.name = newName
          this.group.list.splice(index, 1, obj)
        }
      }, 100)
    },
    deleteGroup(index) {
      this.groupIndex = index

      let that = this
      setTimeout(() => {
        if (confirm(this.i18n.optionGroupDelete)) {
          if (that.group.list.length === 1) {
            this.showTips(this.i18n.optionGroupDeleteAtLeaseOne)
          } else {
            // 设置分组索引
            that.groupIndex = index - 1 < 0 ? 0 : index - 1
            // 设置分组内容
            that.group.list.splice(index, 1)
          }
        }
      }, 100)
    },
    selectGroup(index) {
      this.groupIndex = index
    },
    extClick(item) {
      this.rightMenu.showClass = 'click'

      let listObj = this.group.list[this.groupIndex]
      if (item.isLocked) {
        delete listObj.lock[item.id]
      } else {
        listObj.lock[item.id] = 1
      }
      this.group.list.splice(this.groupIndex, 1, listObj)
      this.showTips(this.i18n.tipSetSuc)
    },
    extClickFix(item) {
      this.rightMenu.showClass = 'click'
      //为了强制刷新
      // let listObj = this.group.list[this.groupIndex]
      // this.group.list.splice(this.groupIndex, 1, listObj)
      //强制刷新 利用v-if
      this.group2 = false
      this.group2 = true

      if (item.isFixed) {
        delete this.fix[item.id]
        item.isFixed = false
      } else {
        this.fix[item.id] = 1
        item.isFixed = true
      }
      Storage.set('_fix_', this.fix)

      if (item.isFixed) {
        Extension.on(item)
      } else {
        Extension.off(item)
      }

      this.showTips(this.i18n.tipSetSuc)
    },

    extClickRule(item) {
      // 强制刷新
      this.ruleExtShow = false
      this.ruleExtShow = true
      if (item.isRuled) {
        delete this.currule.ids[item.id]
        item.isRuled = false
      } else {
        this.currule.ids[item.id] = 1
        item.isRuled = true
      }

      // this.showTips(this.i18n.tipSetSuc)
    },

    addGroup() {
      this.group.list.push({
        name: this.i18n.newGroupName,
        lock: {},
      })
      this.groupIndex = this.group.list.length - 1
    },
    // 重置点击生成的rank
    resetRank(e) {
      Storage.remove('_rankList_')
      this.showTips(this.i18n.tipResetRank)
    },
    modifyName(item) {
      setTimeout(() => {
        var newName =
          prompt(this.i18n.optionNameModifyName + '\n' + item.shortName, item.nickName) ||
          item.nickName
        if (newName.trim()) {
          item.nickName = Util.formatNickName(newName, 8)
          localStorage.setItem('_itemNick_' + item.id, item.nickName)
        }
      }, 100)
    },
    // 反馈
    // feedback(e) {
    //   let email = this.$refs.feedbackEmail.value
    //   let emailValid = email.length > 0 && /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)
    //   let msg = this.$refs.feedbackMsg.value
    //   let msgValid = msg.length > 0

    //   let _this = this
    //   if (emailValid && msgValid) {
    //     this.showTips(this.i18n.feedback_process, 20000)
    //     Util.ajax({
    //       methods: 'POST',
    //       url: 'https://extensions.extfun.com/ajax/feedback',
    //       data: {
    //         email: email,
    //         msg: msg,
    //         version: chrome.runtime.getManifest().version,
    //         ua: navigator.userAgent,
    //         lang: chrome.i18n.getUILanguage(),
    //         screenshots: JSON.stringify(_this.uploadedFiles),
    //       },
    //       success: function (res) {
    //         _this.showTips(_this.i18n.feedback_success)
    //         _this.$refs.feedbackEmail.value = ''
    //         _this.$refs.feedbackMsg.value = ''
    //         _this.uploadedFiles = []
    //       },
    //       faile: function (res) {
    //         console.error(res)
    //         _this.showTips(_this.i18n.feedback_fail)
    //       },
    //     })
    //   } else {
    //     emailValid
    //       ? msgValid || this.$refs.feedbackMsg.classList.add('error')
    //       : this.$refs.feedbackEmail.classList.add('error')
    //     setTimeout(() => {
    //       this.$refs.feedbackMsg.classList.remove('error')
    //       this.$refs.feedbackEmail.classList.remove('error')
    //     }, 700)
    //   }
    // },
    handleFile(e) {
      let _this = this
      let files = e.target.files
      if (_this.uploadedFiles.length + files.length > 2) {
        window.alert(_this.i18n.feedback_error_amount)
        return
      }
      for (const file of files) {
        if (!file.type.match('image')) continue
        if ((file.size / 1024).toFixed(4) > 1024) {
          window.alert(_this.i18n.feedback_error_filesize)
          continue
        }
        const reader = new FileReader()
        reader.onload = function (e) {
          _this.uploadedFiles.push({ src: e.target.result, name: file.name })
        }
        reader.onerror = function (error) {
          console.error(error)
        }
        reader.readAsDataURL(file)
      }
      // 必须清空 否则可能导致相同文件再次选择不能触发change事件
      e.srcElement.value = ''
    },
    closeFile(index) {
      this.uploadedFiles.splice(index, 1)
    },
    // 更改扩展显示列数
    changeWindowSize(e) {
      let newSize = e.target.value
      this.showWindowSize = newSize
      Storage.set('_showColumn_', newSize)
      this.showTips(this.i18n.tipSetSuc)
    },
    // 更改显示图标大小
    changeIconSize(e) {
      let newSize = e.target.value
      this.showIconSize = newSize
      Storage.set('_showIconSize_', newSize)
      this.showTips(this.i18n.tipSetSuc)
    },
    // 更改扩展排序
    changeSortType(value) {
      if (this.sortType !== value) {
        this.sortType = value
        Storage.set('_radio_ext_sort_', value)
        this.showTips(this.i18n.tipSetSuc)
      }
    },
    // 更改菜单弹出方式
    changeMenuType(value) {
      if (this.menuType !== value) {
        this.menuType = value
        Storage.set('_radio_ext_menu_', value)
        this.showTips(this.i18n.tipSetSuc)
      }
    },
    // 更改显示模式
    changeViewMod(value) {
      if (this.viewMod !== value) {
        this.viewMod = value
        Storage.set('_showViewMod_', value)
        this.showTips(this.i18n.tipSetSuc)
      }
      if (value == 'column') {
        document.getElementById('windowSizeRange').disabled = true
        document.getElementById('iconSizeRange').disabled = true
        document.getElementById('windowSizeRange').style.background = '#808080'
        document.getElementById('windowSizeRange').style.border = '1px solid #808080'
        document.getElementById('iconSizeRange').style.background = '#808080'
        document.getElementById('iconSizeRange').style.border = '1px solid #808080'
      } else {
        document.getElementById('windowSizeRange').disabled = false
        document.getElementById('iconSizeRange').disabled = false
        document.getElementById('windowSizeRange').style.background = 'rgba(100, 148, 244, 0.8)'
        document.getElementById('windowSizeRange').style.border = '1px solid #6494f4'
        document.getElementById('iconSizeRange').style.background = 'rgba(100, 148, 244, 0.8)'
        document.getElementById('iconSizeRange').style.border = '1px solid #6494f4'
      }
    },
    changeAppearance(value) {
      switch (value) {
        case 'dark':
          Storage.set('_showModTheme_', 'manual')
          Storage.set('_showViewTheme_', 'dark')
          break
        case 'light':
          Storage.set('_showModTheme_', 'manual')
          Storage.set('_showViewTheme_', '')
          break
        default:
          Storage.set('_showModTheme_', 'automatic')
          Storage.set('_showViewTheme_', '')
      }
      this.showTips(this.i18n.tipSetSuc)
    },

    getItemIcon(itemid) {
      let src
      this.extList.filter((item) => {
        if (item.id === itemid) {
          src = item['showIcon']
        }
      })
      return src
    },

    // 创建规则 初始化创建界面
    addRule() {
      //初始化数据
      //强制刷新
      this.ruleIndex = -2
      this.ruleIndex = -1
      this.ruleExtShow = true
      this.$refs.ruleUrl.value = ''
      //初始化当前rule
      this.currule = {
        action: 'enableOnly',
        ids: {},
        match: {
          mod: '',
          url: '',
          regExp: '',
        },
        disabled: false,
      }
    },

    cancelRule() {
      //初始化数据
      //强制刷新
      this.ruleExtShow = false
    },

    // 保存规则
    saveRule() {
      if (this.ruleExtShow) {
        let _this = this
        //最多10条规则 每个规则最多12个扩展
        if (this.rules.list && this.rules.list.length >= 10) {
          window.alert(_this.i18n.ruleMaxNum + '10')
          return
        }
        let url = this.$refs.ruleUrl.value

        //校验
        if (url && url.trim()) {
          url = Util.formatUrl(url)
          this.currule.match.url = url.trim()
          //预编译url
          this.currule.match.regExp = Util.analyze(url)

          this.currule.action = this.$refs.ruleModel.value
        } else {
          this.$refs.ruleUrl.value = ''
          window.alert(_this.i18n.ruleUrlEmpty)
          return
        }

        if (Object.keys(this.currule.ids).length === 0) {
          window.alert(_this.i18n.ruleExtEmpty)
          return
        }

        if (Object.keys(this.currule.ids).length > 12) {
          window.alert(_this.i18n.ruleMaxExtNum + '12')
          return
        }

        if (this.ruleIndex === -1) {
          this.rules.list.push(this.currule)
        } else {
          this.rules.list.splice(this.ruleIndex, 1, this.currule)
        }
        //保存后隐藏 初始化交给addrule
        this.ruleExtShow = false

        //同步_rule_
        Storage.set('_rule_', this.rules)
      }
    },

    editRule(item, index) {
      // 强制刷新
      this.ruleIndex = -1
      this.ruleIndex = index
      //深拷贝
      this.currule = JSON.parse(JSON.stringify(item))
      this.$refs.ruleUrl.value = this.currule.match.url
      this.$refs.ruleModel.value = this.currule.action
      this.ruleExtShow = true
    },

    deleteRule(index) {
      let that = this

      setTimeout(() => {
        if (confirm(this.i18n.optionRuleDelete)) {
          that.rules.list.splice(index, 1)
          that.ruleIndex = -1
          that.ruleExtShow = false
          Storage.set('_rule_', this.rules)
        }
      }, 100)
    },

    copyRule(item, index) {
      let that = this
      this.rules.list.push(JSON.parse(JSON.stringify(item)))
    },

    changeRuleStatus(index, status) {
      this.rules.list[index].disabled = !status
      //同步_rule_
      Storage.set('_rule_', this.rules)
    },

    ruleTip() {
      this.showDialog = true
      this.dialogType = 'confirm'
      this.dialogTitle = this.i18n.ruleTipTitle1
      let content = this.i18n.ruleTipContent1
      this.dialogContent = content
      this.confirmText = this.i18n.tipConfirmText
    },

    matchTip() {
      this.showDialog = true
      this.dialogType = 'confirm'
      this.dialogTitle = this.i18n.ruleTipTitle2
      let content = this.i18n.ruleTipContent2
      this.dialogContent = content
      this.dialogWidth = '600px'
      this.confirmText = this.i18n.tipConfirmText
    },

    importOption(e) {
      const file = e.target.files[0]
      const reader = new FileReader()
      let _this = this
      reader.onload = function (e) {
        let optionsString = e.target.result
        if (!optionsString.match(/^EManager-Options/)) {
          window.alert(_this.i18n.failed_to_import)
          return
        }
        let options = null
        try {
          options = JSON.parse(optionsString.substr(17))
        } catch (e) {
          console.log(e)
          window.alert(_this.i18n.failed_to_import)
          return
        }
        if (!options) {
          window.alert(_this.i18n.failed_to_import)
          return
        }
        let settings = options['settings']
        chrome.storage.sync.set(settings, function () {
          Object.keys(options['nicknames']).forEach((item) =>
            localStorage.setItem(item, options['nicknames'][item])
          )
          window.alert(_this.i18n.success_to_import)
        })
      }
      reader.readAsText(file, 'UTF-8')
    },

    exportOption() {
      chrome.storage.sync.get((optionData) => {
        let dataURI = 'data:text;charset=utf-8,EManager-Options:'

        let nickData = {}
        Object.keys(localStorage).forEach((item) =>
          item.indexOf('_itemNick_') != -1 ? (nickData[item] = localStorage.getItem(item)) : ''
        )

        let resultJsonObject = {}
        resultJsonObject['settings'] = optionData
        resultJsonObject['nicknames'] = nickData

        dataURI = dataURI + JSON.stringify(resultJsonObject)

        const a = document.createElement('a')
        a.href = dataURI
        a.download = 'EManager.options'
        a.style.display = 'none'
        document.body.appendChild(a)
        a.click()
        a.remove()
      })
    },

    // 提示组件
    showTips: (function () {
      let timer = null
      return function (text, timenum = 1500) {
        if (text) {
          let t = this
          clearTimeout(timer)
          t.tips.show = true
          t.tips.content = text
          timer = setTimeout(function () {
            t.tips.show = false
            setTimeout(() => {
              t.tips.content = ''
            }, 100)
          }, timenum)
        }
      }
    })(),
  },

  // 初始化
  async beforeMount() {
    window._vm = this

    let _storage = await Storage.getAll(this)

    let _allExt = await Extension.getAll(this)

    let activeTab = '#general'

    if (window.location && window.location.hash) {
      ;[activeTab] = window.location.hash.split('_')
    }

    switch (activeTab) {
      case '#general':
        this.generalShow = true
        this.menuName = 'viewMenu'
        break
      case '#group':
        // this.groupShow = true;
        this.group1 = true
        this.group2 = false
        this.menuName = 'groupMenu'
        break
      case '#feedback':
        this.feedbackShow = true
        this.menuName = 'feedbackMenu'
        break
      case '#name':
        this.nameShow = true
        this.menuName = 'nameMenu'
        break
      case '#rule':
        this.ruleShow = true
        this.menuName = 'ruleMenu'
        break
      default:
        this.generalShow = true
        this.menuName = 'viewMenu'
        break
    }

    // 显示初始化：图标大小、宽度等
    let showWindowSize = Storage.get('_showColumn_')
    if (showWindowSize) {
      this.showWindowSize = showWindowSize
    }
    let showIconSize = Storage.get('_showIconSize_')
    if (showIconSize) {
      this.showIconSize = showIconSize
    }

    let sortType = Storage.get('_radio_ext_sort_')
    if (sortType) {
      this.sortType = sortType
    }

    let menuType = Storage.get('_radio_ext_menu_')
    if (menuType) {
      this.menuType = menuType
    }

    let viewMod = Storage.get('_showViewMod_') || 'row'
    this.viewMod = viewMod

    if (viewMod == 'column') {
      document.getElementById('windowSizeRange').disabled = true
      document.getElementById('iconSizeRange').disabled = true
      document.getElementById('windowSizeRange').style.background = '#808080'
      document.getElementById('windowSizeRange').style.border = '1px solid #808080'
      document.getElementById('iconSizeRange').style.background = '#808080'
      document.getElementById('iconSizeRange').style.border = '1px solid #808080'
    } else {
      document.getElementById('windowSizeRange').disabled = false
      document.getElementById('iconSizeRange').disabled = false
      document.getElementById('windowSizeRange').style.background = 'rgba(100, 148, 244, 0.8)'
      document.getElementById('windowSizeRange').style.border = '1px solid #6494F4'
      document.getElementById('iconSizeRange').style.background = 'rgba(100, 148, 244, 0.8)'
      document.getElementById('iconSizeRange').style.border = '1px solid #6494F4'
    }

    let themeMod = Storage.get('_showModTheme_') || 'manual'
    // 手动模式
    if (themeMod === 'manual') {
      if (Storage.get('_showViewTheme_') === 'dark') {
        this.themeType = 'dark'
      } else {
        this.themeType = 'light'
      }
    } else {
      this.themeType = 'automatic'
    }
    // 增加分组功能，兼容老版本问题
    Storage.initGroup(this)

    this.extList = _allExt

    Util.init(this)
  },
}
</script>

