import * as Extension from './extension'

// 右键菜单宽度
const RightMenuWidth = chrome.i18n.getUILanguage() === 'ru' ? 210 : 190

//正则表达式中的特殊字符
//'\\[\^$.|?*+(){}/';
const regExpMetaChars = {
  92: true,
  91: true,
  94: true,
  36: true,
  46: true,
  124: true,
  63: true,
  42: true,
  43: true,
  40: true,
  41: true,
  123: true,
  125: true,
  47: true,
}

// option页面vue对象
let vm = null

let hoverTimer = null

/**
 * 初始化方法
 */
function init(t) {
  // 指向vm对象
  vm = t

  // 屏蔽右键菜单选项
  document.addEventListener('contextmenu', function (e) {
    e.preventDefault()
  })

  // 存储扩展版本号
  localStorage.version = chrome.runtime.getManifest().version
}

/**
 * 通过扩展获取右键菜单、扩展名称的显示位置
 */
function getPositionByExt(item, info) {
  const itemEle = document.querySelector(`[data-id=${item.id}]`)
  const showGap = 16

  const extSize = itemEle.offsetHeight
  const bodyWidth = document.querySelector('#option').offsetWidth
  let extLeft = itemEle.offsetLeft
  let extTop = itemEle.offsetTop
  let atLeft = false
  let adviseMaxWidth = null
  let left = 0
  let right = 0
  let top = 0

  // 计算横向边距
  let rightSideSpace = bodyWidth - extSize - extLeft - showGap
  let leftSideSpace = extLeft - showGap

  if (rightSideSpace > 420 || leftSideSpace < rightSideSpace) {
    left = `${extLeft + extSize + showGap}px`
    right = 'unset'
    adviseMaxWidth = rightSideSpace - 10
  } else {
    atLeft = true
    adviseMaxWidth = leftSideSpace - 10
    left = 'unset'
    right = `${bodyWidth - extLeft + showGap - 40 - 175}px`
  }

  // 计算上边距
  if (info.height > extSize) {
    top = `${extTop - (info.height - extSize) / 2}px`
  } else {
    top = `${extTop + (extSize - info.height) / 2}px`
  }

  // console.table({
  //   extSize, extLeft, extTop, showMaxWidth: item.showMaxWidth, rightSideSpace, leftSideSpace, atLeft, adviseMaxWidth, left, right, top
  // })

  return {
    left,
    right,
    top,
    atLeft,
    adviseMaxWidth,
  }
}

/**
 * 显示右键菜单
 */
function showMenu(item) {
  hideMenu()
  clearTimeout(hoverTimer)
  hoverTimer = null

  setTimeout(() => {
    // 右键菜单内容
    let position = getPositionByExt(item, {
      width: RightMenuWidth,
      height: 84,
    })

    let content = ''
    vm.rightMenu = {
      name: item.shortName,
      showClass: position.atLeft ? 'showInfoLeft' : 'showInfoRight',
      left: position.left,
      right: position.right,
      top: position.top,
      backgroundColor: item.showColor['original'],
      content,
    }
  }, 0)
}

/**
 * 隐藏右键菜单
 */
function hideMenu() {
  vm.rightMenu.showClass = ''
}

/**
 * 初始化页面所有的操作
 */
function resetHandle(params) {
  // 关闭右键菜单
  hideMenu()

  // 关闭Hover
  clearTimeout(hoverTimer)
  hoverTimer = null
  vm.extList.forEach((item) => {
    if (item.isHover) {
      item.isHover = false
    }
  })
}

function resetHandle2(params) {
  // 关闭右键菜单
  hideMenu()

  // 关闭Hover
  clearTimeout(hoverTimer)
  hoverTimer = null
  vm.extList.forEach((item) => {
    if (item.isHover) {
      item.isHover = false
    }
  })

  vm.hover.doing = false
  vm.hover.listName = ''
}

/**
 * 进入扩展图标时
 */
function enter(item) {
  clearTimeout(hoverTimer)
  hoverTimer = null

  if (!item.isHover) {
    resetHandle()
    hoverTimer = setTimeout(() => {
      item.isHover = true
      if (vm.rightMenu.showClass.trim() === 'click') {
        // empty
      } else {
        showMenu(item)
      }
      hoverTimer = null
    }, 200)
  }
}
// 离开
function leave(item) {
  resetHandle()
  // eslint-disable-next-line no-empty
  if (vm.rightMenu.showClass.trim()) {
    // empty
  } else {
    if (hoverTimer) {
      clearTimeout(hoverTimer)
      hoverTimer = null
    }
    resetHandle()
  }
}

/**
 * 搜索功能
 */
function search() {
  let text = vm.searcher.text
    .trim()
    .replace(/\s{2,}/g, ' ')
    .toLowerCase()
  if (text) {
    setTimeout(() => {
      vm.searcher.doing = true
      let queryArr = text.split(/\s/)
      let searchRes = vm.ext.extList.filter((item) => {
        let extInfo = (item.name + item.description).toLowerCase()
        let isSearched = queryArr.some((q) => extInfo.includes(q))
        item['isSearched'] = isSearched
        return isSearched
      })
      if (searchRes.length === 0) {
        vm.searcher.empty = true
      } else {
        vm.searcher.empty = false
      }
    })
  } else {
    vm.searcher.doing = false
    vm.searcher.empty = false
    vm.ext.extList.forEach((item) => {
      if (item['isSearched']) {
        item['isSearched'] = false
      }
    })
  }
}

function cancelSearch() {
  vm.searcher = {
    doing: false,
    text: '',
    empty: false,
  }
}

// 开启关闭扩展
function onoff(item) {
  // 防止Hover延迟在点击后生效
  clearTimeout(hoverTimer)
  hoverTimer = null

  Extension.onoff(item)
  resetHandle()
}
// 重置
function clear() {
  Extension.clear()
}

/**
 * 分组
 */
let groupTimer = null

function showGroup() {
  groupTimer = setTimeout(() => {
    vm.groupShow = true
  }, 200)
}

function hideGroup() {
  clearTimeout(groupTimer)
  vm.groupShow = false
}

function changeGroup(index) {
  vm.groupIndex = index
  vm.groupShow = false

  setTimeout(() => {
    let lockObj = vm.group.list[index].lock
    vm.extList.forEach((item) => {
      // 开启状态
      if (item.enabled && !lockObj[item.id]) {
        chrome.management.setEnabled(item.id, false)
        item.enabled = false
      } else if (!item.enabled && lockObj[item.id]) {
        chrome.management.setEnabled(item.id, true)
        item.enabled = true
      }
    })
    resetHandle()
    chrome.action.setBadgeText({
      text: '',
    })
  }, 50)
}

function setGroup() {
  vm.groupShow = false
  chrome.tabs.create({
    url: `${chrome.runtime.getManifest().options_page}#group`,
  })
}

