function formatNickName(text, length) {
  if (text.replace(/[\u4e00-\u9fa5]/g, 'aa').length <= length) {
    return text
  } else {
    var _length = 0
    var outputText = ''
    for (var i = 0; i < text.length; i++) {
      if (/[\u4e00-\u9fa5]/.test(text[i])) {
        _length += 2
      } else {
        _length += 1
      }
      if (_length > length) {
        break
      } else {
        outputText += text[i]
      }
    }
    return outputText
  }
}

// # Get the magical regex of this pattern. See
// # https://github.com/FelisCatus/SwitchyOmega/wiki/Host-wildcard-condition
// # for the magic.
function shExp2RegExp(pattern, options) {
  var charCodeAsterisk, charCodeQuestion, code, end, i, j, ref, ref1, regex, start, trimAsterisk
  trimAsterisk = (options != null ? options.trimAsterisk : void 0) || false
  start = 0
  end = pattern.length
  charCodeAsterisk = 42
  charCodeQuestion = 63
  if (trimAsterisk) {
    while (start < end && pattern.charCodeAt(start) === charCodeAsterisk) {
      start++
    }
    while (start < end && pattern.charCodeAt(end - 1) === charCodeAsterisk) {
      end--
    }
    if (end - start === 1 && pattern.charCodeAt(start) === charCodeAsterisk) {
      return ''
    }
  }
  regex = ''
  if (start === 0) {
    regex += '^'
  }
  for (
    i = j = ref = start, ref1 = end;
    ref <= ref1 ? j < ref1 : j > ref1;
    i = ref <= ref1 ? ++j : --j
  ) {
    code = pattern.charCodeAt(i)
    switch (code) {
      case charCodeAsterisk:
        regex += '.*'
        break
      case charCodeQuestion:
        regex += '.'
        break
      default:
        if (regExpMetaChars[code] >= 0) {
          regex += '\\'
        }
        regex += pattern[i]
    }
  }
  if (end === pattern.length) {
    regex += '$'
  }
  return regex
}

function safeRegex(expr) {
  try {
    return new RegExp(expr)
  } catch (error) {
    console.log(error)
    // _ = error;
    // Invalid regexp! Fall back to a regexp that does not match anything.
    return /(?!)/
  }
}

function analyze(condition) {
  var parts, pattern
  parts = (function () {
    var j, len, ref1, results
    ref1 = condition.split('|')
    results = []
    for (j = 0, len = ref1.length; j < len; j++) {
      pattern = ref1[j]
      if (!pattern) {
        continue
      }
      if (pattern.charCodeAt(0) === '.'.charCodeAt(0)) {
        pattern = '*' + pattern
      }
      if (pattern.indexOf('**.') === 0) {
        results.push(
          shExp2RegExp(pattern.substring(1), {
            trimAsterisk: true,
          })
        )
      } else if (pattern.indexOf('*.') === 0) {
        results.push(
          shExp2RegExp(pattern.substring(2), {
            trimAsterisk: false,
          })
            .replace(/./, '(?:^|\\.)')
            .replace(/\.\*\$$/, '')
        )
      } else {
        results.push(
          shExp2RegExp(pattern, {
            trimAsterisk: true,
          })
        )
      }
    }
    return results
  })()
  // return safeRegex(parts.join('|'));
  return parts.join('|')
}

function formatUrl(condition) {
  condition = condition.replace(/\s+/g, '')
  var results, len, pattern, j
  results = []
  var ref = condition.split('|')
  for (j = 0, len = ref.length; j < len; j++) {
    pattern = ref[j]
    //如果www开头 则进行替换
    if (pattern.startsWith('www')) {
      pattern = pattern.replace('www', '*')
    }
    //如果斜杠结尾 则进行替换
    if (pattern.charCodeAt(pattern.length - 1) === 47) {
      pattern = pattern.substring(0, pattern.length - 1)
    }

    if (!pattern.startsWith('*') && !pattern.startsWith('?')) {
      pattern = '*' + pattern
    }
    results.push(pattern)
  }
  return results.join('|')
}

export {
  init,
  showMenu,
  enter,
  leave,
  search,
  onoff,
  clear,
  cancelSearch,
  showGroup,
  hideGroup,
  changeGroup,
  setGroup,
  heartDetector,
  resetHandle,
  formatNickName,
  analyze,
  formatUrl,
}
