<template>
  <div class="tree-menu">
    <ul>
      <li id="menutab" data-name="viewMenu" :class="{active:'viewMenu' == activeName}" @click.stop="toggle($event)" >
        <a href="#general" οnclick="return false;">{{i18n['viewSetMenu']}}</a>
      </li>
      <li id="menutab" data-name="groupMenu" :class="{active:'groupMenu' == activeName}" @click.stop="toggle($event)">
        <a href="#group">{{i18n['optionGroupTitleMenu']}}</a>
        <ul v-show="open">
          <li id="menutabSub" data-name="groupMenuCommon" @click.stop="togglesub($event)" :class="{active:'groupMenuCommon' == activeSubName}"><a>{{i18n['commonGroup']}}</a></li>
          <li id="menutabSub" data-name="groupMenuFix" @click.stop="togglesub($event)" :class="{active:'groupMenuFix' == activeSubName}"><a>{{i18n['fixGroup']}}</a></li>
        </ul>
      </li>
      <li id="menutab" data-name="nameMenu" :class="{active:'nameMenu' == activeName}" @click.stop="toggle($event)">
        <a href="#name">{{i18n['nameMenu']}}</a>
      </li>
      <li id="menutab" data-name="ruleMenu" :class="{active:'ruleMenu' == activeName}" @click.stop="toggle($event)">
        <a href="#rule">{{i18n['ruleMenu']}}</a>
      </li>
      <li id="menutab" data-name="feedbackMenu" :class="{active:'feedbackMenu' == activeName}" @click.stop="toggle($event)">
        <a href="#feedback">{{i18n['feedbackMenu']}}</a>
      </li>
    </ul>
  </div>
  </template>
  <script>
  import i18n from '@/lib/i18n'
  export default {
    name: "TreeMenu",
    props: {
      name: String,
    },
    data() {
      return {
        i18n: i18n(),
        open: false,
        activeName: this.name,
        activeSubName: ''
      };
    },
    watch: {
      // 子组件需要根据父组件的值来初始化自己
      name: function(val, oldVal) {
        this.activeName = val
        if(val === 'groupMenu') {
          this.activeSubName = 'groupMenuCommon'
          this.open = true
        }
      }
    },
    methods: {
      togglesub: function(e) {
        let menuSubName = e.currentTarget.getAttribute('data-name')
        this.activeSubName = menuSubName
        // 能触发到menuSub 父菜单一定已经先先被触发到
        if(menuSubName === 'groupMenuCommon') {
          this.$parent.group1 = true
          this.$parent.group2 = false
        }
        if(menuSubName === 'groupMenuFix') {
          this.$parent.group2 = true
          this.$parent.group1 = false
        }
  
        //submenu可能会引起页面有滚动条
        let targetOffsetTop = document.getElementById("group").offsetTop
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
  
        const STEP = 50;
  
        if (scrollTop > targetOffsetTop) {
          // 往上滑
          smoothUp();
        } 
  
        function smoothUp() {
          if (scrollTop > targetOffsetTop) {
            if (scrollTop - targetOffsetTop >= STEP) {
              scrollTop -= STEP;
            } else {
              scrollTop = targetOffsetTop;
            }
            document.body.scrollTop = scrollTop;
            document.documentElement.scrollTop = scrollTop;
            requestAnimationFrame(smoothUp);
          }
        }
  
      },
      toggle: function(e) {
        let menuName = e.currentTarget.getAttribute('data-name')
        this.activeName = menuName
        if(menuName === 'groupMenu') {
          this.activeSubName = 'groupMenuCommon'
          this.open = !this.open
        } else {
          this.open = false
        }
        switch (menuName) {
          case "viewMenu":
            this.$parent.generalShow = true
            this.$parent.group1 = false
            this.$parent.group2 = false
            this.$parent.feedbackShow = false
            this.$parent.supportShow = false
  
            this.$parent.nameShow = false
            this.$parent.ruleShow = false
            break;
          case "groupMenu":
            this.$parent.group1 = true
            this.$parent.group2 = false
  
            this.$parent.generalShow = false
            this.$parent.feedbackShow = false
            this.$parent.supportShow = false
  
            this.$parent.nameShow = false
            this.$parent.ruleShow = false
            break;
          case "feedbackMenu":
            this.$parent.feedbackShow = true
            // 清空附件
            // this.$parent.uploadedFiles = []
            this.$parent.group1 = false
            this.$parent.group2 = false
  
            this.$parent.generalShow = false
            this.$parent.supportShow = false
  
            this.$parent.nameShow = false
            this.$parent.ruleShow = false
            break;
          case "supportMenu":
            this.$parent.supportShow = true
  
            this.$parent.group1 = false
            this.$parent.group2 = false
  
            this.$parent.feedbackShow = false
            this.$parent.generalShow = false
  
            this.$parent.nameShow = false
            this.$parent.ruleShow = false
            break;
          case "nameMenu":
            this.$parent.nameShow = true
  
            this.$parent.group1 = false
            this.$parent.group2 = false
  
            this.$parent.feedbackShow = false
            this.$parent.generalShow = false
            this.$parent.supportShow = false
            this.$parent.ruleShow = false
            break;
  
          case "ruleMenu":
            this.$parent.ruleShow = true
  
            this.$parent.nameShow = false
  
            this.$parent.group1 = false
            this.$parent.group2 = false
  
            this.$parent.feedbackShow = false
            this.$parent.generalShow = false
            this.$parent.supportShow = false
  
            //初始化动作
            this.$parent.ruleExtShow = false
  
            break;  
  
          default:
            this.$parent.generalShow = true
  
            this.$parent.group1 = false
            this.$parent.group2 = false
  
            this.$parent.feedbackShow = false
            this.$parent.supportShow = false
  
            this.$parent.nameShow = false
            this.$parent.ruleShow = false
        }
  
        //nameMenu高度可能导致有滚动条
        if(menuName==='nameMenu') {
          //submenu可能会引起页面有滚动条
          //有可能在执行本方法的时候 nameMenu还未加载  this.$parent.nameShow = true有延迟
          if(document.getElementById("group3")) {
            let targetOffsetTop = document.getElementById("group3").offsetTop
            let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            const STEP = 50;
            if (scrollTop > targetOffsetTop) {
              // 往上滑
              smoothUp();
            } 
  
            function smoothUp() {
              if (scrollTop > targetOffsetTop) {
                if (scrollTop - targetOffsetTop >= STEP) {
                  scrollTop -= STEP;
                } else {
                  scrollTop = targetOffsetTop;
                }
                document.body.scrollTop = scrollTop;
                document.documentElement.scrollTop = scrollTop;
                requestAnimationFrame(smoothUp);
              }
            }
          }
        }
      }
    }
  };
  </script>
  <style>
