  }
  
  .tree-menu>ul>li>ul>li>a::before {
      content: "-";
      position: absolute;
      left: 12px !important;
  }
  
  .tree-menu>ul>li>a::before {
      width: 32px !important;
      left: 22px !important;
      -webkit-mask-position: left center !important;
      pointer-events: none;
  }
  .tree-menu>ul>li>ul>li>a::before {
      width: 32px !important;
      left: 42px !important;
      -webkit-mask-position: left center !important;
      pointer-events: none;
  }
  
  .tree-menu>ul>li.active>a::before {
      background-color: #ccc;
  }
  
  .tree-menu>ul>li.active::after {
      content: "";
