  }
  
  .tree-menu>ul>li>ul>li.active>a {
      font-weight: 400;
      color: #ccc;
      /* background: rgba(255,255,255,0.07); */
  }
  
  .tree-menu .icon-active {
    /* font-weight: bold; */
    cursor: default;
